<template>
    <page-view class="news-detail">
        <div class="imgbox" v-if="infoStart">
            <img v-lazy="newInfo.ext[1].value[0].url" />
        </div>
        <div class="new-title" v-if="infoStart">
            <p
                style="
                    font-weight: bold;
                    font-size: 0.3rem;
                    padding-bottom: 0.2rem;
                "
            >
                {{ newInfo.title }}
            </p>
            <p style="font-size: 0.22rem; padding-bottom: 0.1rem">
                发布日期：{{ newInfo.start_time.match(/\d{4}-\d{2}-\d{2}/)[0] }}
            </p>
            <div class="base-line"></div>
        </div>
        <div class="new-content"></div>
        <div class="base-line"></div>
        <div class="pageChange">
            <div class="topPage" @click="prevContent($event)">
                <span>&lt</span>
                上一篇
            </div>
            <div class="bottomPage" @click="nextContent">
                下一篇
                <span> > </span>
            </div>
        </div>
        <page-footer />
    </page-view>
</template>

<script>
import { ref, reactive, toRefs, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import Api from "@/utils/api";
export default {
    name: "NewsDetail",
    setup() {
        const route = useRoute();
        const router = useRouter();
        const infoStart = ref(false);
        const info = reactive({
            newInfo: {},
            prevContentId: "",
            nextContentId: "",
        });
        const getnewInfo = async (id) => {
            info.newInfo = await Api.getInfo.getnewInfo({
                contentId: id,
                around: 1,
            });
            infoStart.value = true;
            document.querySelector(".new-content").innerHTML =
                info.newInfo.content;

            // console.log(info.newInfo);
            getaround();
        };
        getnewInfo(route.query.contentId);

        // watch(
        //     () => route.query,
        //     () => {
        //         if (route.query.contentId != undefined) {
        //             getnewInfo(route.query.contentId);
        //         }
        //     }
        // );

        const getaround = async () => {
            let results = await Api.getInfo.getaround({
                channelId: info.newInfo.channelId[0],
                contentId: info.newInfo.contentId,
                start_time: info.newInfo.start_time,
            });
            info.prevContentId = results.prevContent.contentId;
            info.nextContentId = results.nextContent.contentId;
        };

        const prevContent = (e) => {
            e.target.parentElement.parentElement.scrollTop = 0;
            getnewInfo(info.prevContentId);
        };
        const nextContent = (e) => {
            e.target.parentElement.parentElement.scrollTop = 0;
            getnewInfo(info.nextContentId);
        };
        return {
            ...toRefs(info),
            infoStart,
            prevContent,
            nextContent,
        };
    },
};
</script>

<style lang="scss" scoped>
.news-detail {
    padding-top: 1rem;
    background: url("https://www.bh3.com/_nuxt/img/newsbg.a4f7afc.jpg") top
        no-repeat;
    background-size: 100%;
    font-size: 14px;
    .imgbox {
        width: 93vw;
        margin: 20px auto 5px auto;
        border: 1px solid #3dc5eb;
        img {
            width: 100%;
        }
    }
    .new-title {
        position: relative;
        color: #62eaf5;
        padding: 5px 5px 5px 20px;
    }
    .base-line {
        position: absolute;
        width: calc(100% - 14px);
        left: 7px;
        height: 1px;
        background: rgba(98, 234, 245, 0.2);
    }
    .new-content {
        color: white;
        line-height: 28px;
        padding: 0 20px 10px 20px;
    }
    .pageChange {
        width: 94vw;
        margin: 20px auto;
        display: flex;
        justify-content: space-between;
        color: white;
        font-size: 0.28rem;
        div {
            display: flex;
        }
        span {
            display: flex;
            border: 1px solid #0097de;
            align-items: center;
            justify-content: center;
            width: 0.3rem;
            height: 0.46rem;
        }
    }
}

::v-deep img {
    width: 100%;
}
</style>